import type {PointType, RectangleType} from "../../hook/use-move-element/use-move-element-type";

export function getIsInRectangle(point: PointType, rectangle: RectangleType): boolean {
    return (
        point.x >= rectangle.x &&
        point.y >= rectangle.y &&
        point.x <= rectangle.x + rectangle.width &&
        point.y <= rectangle.y + rectangle.height
    );
}

export function getDistance(pointA: PointType, pointB: PointType): number {
    return ((pointA.x - pointB.x) ** 2 + (pointA.y - pointB.y) ** 2) ** 0.5;
}

export function getIsNearToMerge(pointA: PointType, pointB: PointType): boolean {
    const nearToMergeDistance = 40;

    const distance = getDistance(pointA, pointB);

    return distance <= nearToMergeDistance;
}

export function getNearestPoint<LikePointType extends PointType>(
    point: PointType,
    pointList: Array<LikePointType>
): LikePointType | null {
    const firstPoint = pointList.at(0);

    if (!firstPoint) {
        return null;
    }

    return pointList.reduce<LikePointType>(
        (previousValue: LikePointType, currentValue: LikePointType): LikePointType => {
            const previousDistance = getDistance({x: previousValue.x, y: previousValue.y}, point);
            const currentDistance = getDistance({x: currentValue.x, y: currentValue.y}, point);

            return currentDistance < previousDistance ? currentValue : previousValue;
        },
        firstPoint
    );
}

export function getNearestToMerge<LikePointType extends PointType>(
    point: PointType,
    pointList: Array<LikePointType>
): LikePointType | null {
    const nearestPoint = getNearestPoint<LikePointType>(point, pointList);

    if (!nearestPoint) {
        return null;
    }

    if (getIsNearToMerge(point, {x: nearestPoint.x, y: nearestPoint.y})) {
        return nearestPoint;
    }

    return null;
}
