import type {ReactNode} from "react";

import {CraftProvider} from "../../provider/craft/craft-context";
import {LocalizationProvider} from "../../provider/locale/locale-context";
import {ThemeProvider} from "../../provider/theme/theme-context";

interface PropsType {
    readonly children: ReactNode;
}

export function AppProvider(props: PropsType): JSX.Element {
    const {children} = props;

    return (
        <LocalizationProvider>
            <ThemeProvider>
                <CraftProvider>{children}</CraftProvider>
            </ThemeProvider>
        </LocalizationProvider>
    );
}
