import {createContext, type ReactNode, useCallback, useMemo, useState} from "react";

import type {RectangleType, UseMoveElementHookMoveEventType} from "../../hook/use-move-element/use-move-element-type";
import {getRandomNumber} from "../../util/number";
import {getRandomString} from "../../util/string";
import {defaultCraftContext, elementFieldCssSelector} from "./craft-context-const";
import {getIsInRectangle, getNearestToMerge} from "./craft-context-helper";
import type {CraftContextType, CraftedElementType} from "./craft-context-type";

export const CraftContext = createContext<CraftContextType>(defaultCraftContext);

const {Provider: CraftContextProvider} = CraftContext;

interface ThemeContextPropsType {
    readonly children: ReactNode;
}

export function CraftProvider(props: ThemeContextPropsType): JSX.Element {
    const {children} = props;
    const [elementList, setElementList] = useState<Array<CraftedElementType>>(defaultCraftContext.elementList);
    const [craftedElementList, setCraftedElementList] = useState<Array<CraftedElementType>>(
        defaultCraftContext.craftedElementList
    );
    const [fieldFrame, setFieldFrame] = useState<RectangleType>({height: 1000, width: 1000, x: 0, y: 0});

    console.log(setCraftedElementList);

    /*
    Const updateCoveredElement = useCallback((moveNodeId: string, point: PointType) => {
        setElementList((currentElementList: Array<CraftedElementType>): Array<CraftedElementType> => {
            const filteredElementList: Array<CraftedElementType> = currentElementList
                .map<CraftedElementType>((elementToMap: CraftedElementType): CraftedElementType => {
                    return {...elementToMap, isCoveredToMerge: false};
                })
                .filter<CraftedElementType>((elementToFilter: CraftedElementType): elementToFilter is CraftedElementType => {
                    return elementToFilter.id !== moveNodeId;
                });

            const elementToMerge = getNearestToMerge<CraftedElementType>(point, filteredElementList);

            if (elementToMerge) {
                elementToMerge.isCoveredToMerge = true;
            }

            return [...currentElementList];
        });
    }, []);
*/

    const addElementOnField = useCallback((element: CraftedElementType): undefined => {
        const isRandomPosition: boolean = Number.isNaN(element.x) && Number.isNaN(element.y);

        const elementField: HTMLDivElement =
            document.querySelector<HTMLDivElement>(elementFieldCssSelector) ?? document.createElement("div");

        const elementFieldRectangle: DOMRect = elementField.getBoundingClientRect();

        const newX = isRandomPosition
            ? getRandomNumber(elementFieldRectangle.x + 40, elementFieldRectangle.x + elementFieldRectangle.width - 40)
            : element.x;
        const newY = isRandomPosition
            ? getRandomNumber(elementFieldRectangle.y + 40, elementFieldRectangle.y + elementFieldRectangle.height - 40)
            : element.y;

        setElementList((craftedElementListToExpand: Array<CraftedElementType>): Array<CraftedElementType> => {
            return [...craftedElementListToExpand, {...element, x: newX, y: newY}];
        });
    }, []);

    const onMoveStart = useCallback((eventData: UseMoveElementHookMoveEventType): undefined => {
        console.log({eventData});
        const elementField: HTMLDivElement =
            document.querySelector<HTMLDivElement>(elementFieldCssSelector) ?? document.createElement("div");

        const elementFieldRectangle: DOMRect = elementField.getBoundingClientRect();

        // UpdateCoveredElement(moveNodeId, point);

        setFieldFrame({
            height: elementFieldRectangle.height,
            width: elementFieldRectangle.width,
            x: elementFieldRectangle.x,
            y: elementFieldRectangle.y,
        });
    }, []);

    const onMoving = useCallback(
        (eventData: UseMoveElementHookMoveEventType): undefined => {
            const {moveNode, startNode, point} = eventData;
            // Const moveNodeId: string = moveNode.dataset.id ?? "";

            console.log("moving", {isInRectangle: getIsInRectangle(point, fieldFrame)});

            console.log(point, startNode, moveNode);

            // UpdateCoveredElement(moveNodeId, point);
        },
        [fieldFrame]
    );

    const onMoveEnd = useCallback(
        (eventData: UseMoveElementHookMoveEventType): undefined => {
            const {moveNode, point} = eventData;
            const moveNodeId: string = moveNode.dataset.id ?? "";

            console.log("moveNode");
            console.log(moveNode);

            const icon: string = moveNode.dataset.icon ?? "";
            const text: string = moveNode.dataset.text ?? "";

            const isInRectangle: boolean = getIsInRectangle(point, fieldFrame);

            // eslint-disable-next-line max-statements
            setElementList((currentElementList: Array<CraftedElementType>): Array<CraftedElementType> => {
                const elementInList = currentElementList.find<CraftedElementType>(
                    (elementInListToFind: CraftedElementType): elementInListToFind is CraftedElementType => {
                        return elementInListToFind.id === moveNodeId;
                    }
                );

                let idOfMovedMergeElement = "";

                if (isInRectangle) {
                    if (elementInList) {
                        idOfMovedMergeElement = elementInList.id;
                        elementInList.x = point.x;
                        elementInList.y = point.y;
                    } else {
                        idOfMovedMergeElement = getRandomString();
                        currentElementList.push({
                            icon,
                            id: idOfMovedMergeElement,
                            isCoveredToMerge: false,
                            text,
                            x: point.x,
                            y: point.y,
                        });
                    }

                    const lastElement = currentElementList.at(-1);

                    if (!lastElement) {
                        return [...currentElementList];
                    }

                    const movedElement: CraftedElementType = elementInList ?? lastElement;

                    const filteredElementList: Array<CraftedElementType> = currentElementList
                        .map<CraftedElementType>((elementToMap: CraftedElementType): CraftedElementType => {
                            return {...elementToMap, isCoveredToMerge: false};
                        })
                        .filter<CraftedElementType>(
                            (elementToFilter: CraftedElementType): elementToFilter is CraftedElementType => {
                                return elementToFilter.id !== idOfMovedMergeElement;
                            }
                        );

                    const elementToMerge = getNearestToMerge<CraftedElementType>(point, filteredElementList);

                    if (elementToMerge) {
                        const idListToRemove: Array<string> = [movedElement.id, elementToMerge.id];

                        const filteredList: Array<CraftedElementType> = currentElementList.filter<CraftedElementType>(
                            (elementToFilter: CraftedElementType): elementToFilter is CraftedElementType => {
                                return !idListToRemove.includes(elementToFilter.id);
                            }
                        );

                        const newCraftedElement: CraftedElementType = {
                            ...elementToMerge,
                            id: getRandomString(),
                            x: Math.round((movedElement.x + elementToMerge.x) / 2),
                            y: Math.round((movedElement.y + elementToMerge.y) / 2),
                        };

                        filteredList.push({...newCraftedElement, id: getRandomString()});

                        setCraftedElementList(
                            (currentCraftedElementList: Array<CraftedElementType>): Array<CraftedElementType> => {
                                return [...currentCraftedElementList, {...newCraftedElement, id: getRandomString()}];
                            }
                        );

                        return filteredList;
                    }

                    return [...currentElementList];
                }

                // Remove element from field
                return currentElementList.filter<CraftedElementType>(
                    (elementInListToRemove: CraftedElementType): elementInListToRemove is CraftedElementType => {
                        return elementInListToRemove.id !== moveNodeId;
                    }
                );
            });
        },
        [fieldFrame]
    );

    const providedData: CraftContextType = useMemo<CraftContextType>(() => {
        return {addElementOnField, craftedElementList, elementList, onMoveEnd, onMoveStart, onMoving};
    }, [addElementOnField, craftedElementList, elementList, onMoveStart, onMoveEnd, onMoving]);

    return <CraftContextProvider value={providedData}>{children}</CraftContextProvider>;
}
