import {useContext} from "react";

import {CraftContext} from "../../provider/craft/craft-context";
import type {CraftContextType, CraftedElementType} from "../../provider/craft/craft-context-type";
import {useLocale} from "../../provider/locale/locale-context";
import {Element} from "../element/element";
import * as elementBaseStyle from "./element-base.scss";

export function ElementBase(): JSX.Element {
    const {getLocalizedString} = useLocale();
    const useCraftData: CraftContextType = useContext<CraftContextType>(CraftContext);

    return (
        <>
            <div className={elementBaseStyle.element_base}>
                <div className={elementBaseStyle.element_base__filter_wrapper}>
                    <div className={elementBaseStyle.element_base__filter_text_input__wrapper}>
                        <input
                            className={elementBaseStyle.element_base__filter_text_input}
                            placeholder={getLocalizedString("UI__SEARCH_PLACEHOLDER")}
                            type="text"
                        />
                        {/*<button className={elementBaseStyle.element_base__filter_text_input__clean_search} />*/}
                    </div>
                    <button className={elementBaseStyle.element_base__sort_button} />
                </div>
                <ul className={elementBaseStyle.element_base__element_list}>
                    {useCraftData.craftedElementList.map<JSX.Element>(
                        (elementInList: CraftedElementType): JSX.Element => {
                            return (
                                <li className={elementBaseStyle.element_base__element_list_item} key={elementInList.id}>
                                    <Element
                                        icon={elementInList.icon}
                                        id={elementInList.id}
                                        text={elementInList.text}
                                    />
                                </li>
                            );
                        }
                    )}
                </ul>
            </div>
        </>
    );
}
