/* global document */

import {StrictMode} from "react";
import {createRoot} from "react-dom/client";

import {App} from "./component/app/app";
import {ErrorData} from "./component/error-data/error-data";
import {PageHeader} from "./component/page-header/page-header";
import {Popup} from "./component/popup/popup";
import {ShareButtonList} from "./component/share/share-button-list/share-button-list";
import {selector} from "./const";
import {cssVariable} from "./css/style";
import {disableContextMenu} from "./util/disable-context-menu";

// eslint-disable-next-line jest/require-hook
(function main(): undefined {
    console.log(ErrorData, Popup, ShareButtonList, PageHeader, PageHeader, cssVariable);

    disableContextMenu();

    const nodeWrapper = document.querySelector(selector.appWrapper);

    if (!nodeWrapper) {
        throw new Error("[main]: Can not find appWrapper");
    }

    const appNode: JSX.Element = (
        <StrictMode>
            <App />
        </StrictMode>
    );

    createRoot(nodeWrapper).render(appNode);

    window.Telegram.WebApp.disableVerticalSwipes();
})();
