import {type MutableRefObject, useCallback, useContext, useRef, useState} from "react";
import {createPortal} from "react-dom";

import {useMoveElement} from "../../hook/use-move-element/use-move-element";
import {timeToBeginMovingMS} from "../../hook/use-move-element/use-move-element-const";
import type {PointType, UseMoveElementHookMoveEventType} from "../../hook/use-move-element/use-move-element-type";
import {CraftContext} from "../../provider/craft/craft-context";
import {getNearestToMerge} from "../../provider/craft/craft-context-helper";
import type {CraftContextType, CraftedElementType} from "../../provider/craft/craft-context-type";
import {useOnMount} from "../../util/use-on-mount";
import * as elementStyle from "./element.scss";

interface ElementPropsType {
    text: string;
    icon: string;
    position: PointType;
    id: string;
    isCoveredToMerge: boolean;
}

export function ElementCrafted(props: ElementPropsType): JSX.Element {
    const {icon, text, position, id, isCoveredToMerge} = props;
    const startRef: MutableRefObject<HTMLDivElement> = useRef<HTMLDivElement>(document.createElement("div"));
    const moveRef: MutableRefObject<HTMLDivElement> = useRef<HTMLDivElement>(document.createElement("div"));
    const wrapperRef: MutableRefObject<HTMLDivElement> = useRef<HTMLDivElement>(
        document.querySelector(".js-app-wrapper") ?? document.createElement("div")
    );
    const [portalNode, setPortalNode] = useState<HTMLDivElement | null>(null);
    const [canBeMerge, setCanBeMerge] = useState<boolean>(false);

    const useCraftData: CraftContextType = useContext<CraftContextType>(CraftContext);

    useOnMount(() => {
        setPortalNode(document.querySelector<HTMLDivElement>("[data-theme-name]"));
    });

    const updateCanBeMerge = useCallback(
        (point: PointType) => {
            setCanBeMerge((): boolean => {
                const filteredElementList: Array<CraftedElementType> =
                    useCraftData.elementList.filter<CraftedElementType>(
                        (elementToFilter: CraftedElementType): elementToFilter is CraftedElementType => {
                            return elementToFilter.id !== id;
                        }
                    );

                const elementToMerge = getNearestToMerge<CraftedElementType>(point, filteredElementList);

                return Boolean(elementToMerge);
            });
        },
        [id, useCraftData.elementList]
    );

    const ignoredUseMoveElementData = useMoveElement({
        defaultState: {
            x: position.x,
            y: position.y,
            zoom: 1,
        },
        limit: {
            multiplier: 0.5,
            point: {
                max: {
                    x: -1000,
                    y: -1000,
                },
                min: {
                    x: 1000,
                    y: 1000,
                },
            },
            zoom: {
                max: 1,
                min: 1,
            },
        },
        moveRef,
        onMoveEnd: (eventData: UseMoveElementHookMoveEventType) => {
            const {moveNode, startNode, point} = eventData;

            console.log("end");

            startNode.style.opacity = "1";

            useCraftData.onMoveEnd({
                moveNode,
                point,
                startNode,
            });

            console.log({moveNode, point, startNode});
            setCanBeMerge(false);
        },
        onMoveStart: (eventData: UseMoveElementHookMoveEventType) => {
            const {moveNode, startNode, point} = eventData;

            startNode.style.opacity = "0";

            console.log("start");

            useCraftData.onMoveStart({
                moveNode,
                point,
                startNode,
            });

            console.log({moveNode, point, startNode});

            updateCanBeMerge(point);
        },
        onMoving: (eventData: UseMoveElementHookMoveEventType) => {
            const {moveNode, startNode, point} = eventData;

            console.log("moving");

            useCraftData.onMoving({
                moveNode,
                point,
                startNode,
            });

            updateCanBeMerge(point);
        },
        startRef,
        timeToBeginMovingMS,
        wrapperRef,
    });

    return (
        <>
            <div className={elementStyle.element__crafted} data-is-ready-to-merge={isCoveredToMerge} ref={startRef}>
                <span className={elementStyle.element_text}>{icon}</span>
                <span className={elementStyle.element_text}>{text}</span>
            </div>
            {portalNode
                ? createPortal(
                      <div
                          className={elementStyle.element_to_move}
                          data-icon={icon}
                          data-id={id}
                          data-is-ready-to-merge={canBeMerge}
                          data-text={text}
                          ref={moveRef}
                      >
                          <span className={elementStyle.element_text}>{icon}</span>
                          <span className={elementStyle.element_text}>{text}</span>
                      </div>,
                      portalNode
                  )
                : null}
        </>
    );
}
