import {useContext} from "react";

import {CraftContext} from "../../provider/craft/craft-context";
import type {CraftContextType, CraftedElementType} from "../../provider/craft/craft-context-type";
import {ElementCrafted} from "../element/element-crafted";
import * as elementFieldStyle from "./element-field.scss";

export function ElementField(): JSX.Element {
    const useCraftData: CraftContextType = useContext<CraftContextType>(CraftContext);

    return (
        <div className={elementFieldStyle.element_field} data-name="element-field">
            {useCraftData.elementList.map<JSX.Element>((elementInList: CraftedElementType): JSX.Element => {
                const {x, y, text, id, icon, isCoveredToMerge} = elementInList;
                return (
                    <ElementCrafted
                        icon={icon}
                        id={id}
                        isCoveredToMerge={isCoveredToMerge}
                        key={id}
                        position={{x, y}}
                        text={text}
                    />
                );
            })}
        </div>
    );
}
