import {HashRouter, Route, Routes} from "react-router-dom";

import {ClientFriends} from "../../page/client/client-friends/client-friends";
import {ClientHome} from "../../page/client/client-home/client-home";
import {ClientItems} from "../../page/client/client-items/client-items";
import {ClientSetting} from "../../page/client/client-setting/client-setting";
import {Error404} from "../../page/service/error-404/error-404";
import {appRoute} from "./app-route";

export function AppRouting(): JSX.Element {
    return (
        <HashRouter>
            <Routes>
                <Route Component={ClientHome} path={appRoute.root.path}>
                    <Route Component={ClientSetting} path={appRoute.setting.path} />
                    <Route Component={ClientItems} path={appRoute.items.path} />
                    <Route Component={ClientFriends} path={appRoute.friends.path} />
                </Route>

                <Route Component={Error404} path="*" />
            </Routes>
        </HashRouter>
    );
}
