import {type MutableRefObject, useCallback, useEffect, useMemo, useRef, useState} from "react";

import type {PromiseResolveType} from "../../util/promise";
import {usePreventGesture} from "../../util/use-prevent-gesture";
import * as useMoveElementStyle from "./use-move-element.scss";
import {isTouch} from "./use-move-element-const";
import {
    // FitMovingData,
    fitValue,
    getChildMinMax,
    getSize,
    getStyleString,
    getStyleStringCentered,
    mouseEventToPoint,
    touchToPoint,
} from "./use-move-element-helper";
import type {
    MovingDataType,
    MovingLimitDataType,
    PointType,
    UseMoveElementConfigStateType,
    UseMoveElementConfigType,
    UseMoveElementHookType,
} from "./use-move-element-type";

// eslint-disable-next-line max-statements
export function useMoveElement(config: UseMoveElementConfigType): UseMoveElementHookType {
    usePreventGesture();

    const animationStepTime = 1000;

    const {startRef, moveRef, wrapperRef, limit, defaultState, onMoveEnd, onMoveStart, onMoving, timeToBeginMovingMS} =
        config;
    const {point, zoom, multiplier} = limit;
    const {x: maxX, y: maxY} = point.max;
    const {x: minX, y: minY} = point.min;
    const {min: minZoom, max: maxZoom} = zoom;
    const {x: defaultX, y: defaultY, zoom: defaultZoom} = defaultState;
    const [currentState, setCurrentState] = useState<UseMoveElementConfigStateType>({
        x: defaultX,
        y: defaultY,
        zoom: defaultZoom,
    });
    const lastActiveMovingPointRef: MutableRefObject<PointType> = useRef<PointType>({x: Number.NaN, y: Number.NaN});

    const [isMovingActive, setIsMovingActive] = useState<boolean>(false);

    const movingLimit: MovingLimitDataType = useMemo((): MovingLimitDataType => {
        return {
            maxX,
            maxY,
            maxZoom,
            minX,
            minY,
            minZoom,
        };
    }, [maxY, maxX, maxZoom, minZoom, minX, minY]);

    useEffect(() => {
        if (Number.isNaN(defaultX) && Number.isNaN(defaultY)) return;

        startRef.current.style.transform = getStyleStringCentered({
            x: defaultX,
            y: defaultY,
            zoom: defaultZoom,
        });
    }, [startRef, defaultX, defaultY, defaultZoom]);

    const childStartPoint: PointType = useMemo((): PointType => {
        return {
            x: defaultX,
            y: defaultY,
        };
    }, [defaultX, defaultY]);

    const childMovingPoint: PointType = useMemo((): PointType => {
        return {
            x: defaultX,
            y: defaultY,
        };
    }, [defaultX, defaultY]);

    const moveTouchStartPoint: PointType = useMemo((): PointType => {
        return {
            x: 0,
            y: 0,
        };
    }, []);

    const zoomTouchStartPoint: [PointType, PointType] = useMemo((): [PointType, PointType] => {
        return [
            {x: 0, y: 0},
            {x: 0, y: 0},
        ];
    }, []);

    const childStartZoom: Record<"zoom", number> = useMemo((): Record<"zoom", number> => {
        return {zoom: defaultZoom};
    }, [defaultZoom]);

    const childMovingZoom: Record<"zoom", number> = useMemo((): Record<"zoom", number> => {
        return {zoom: defaultZoom};
    }, [defaultZoom]);

    const mousePressed: Record<"isPressed", boolean> = useMemo((): Record<"isPressed", boolean> => {
        return {isPressed: false};
    }, []);

    /*
        Const fitToFrame = useCallback(() => {
            const childMinMax = getChildMinMax(moveRef.current, movingLimit);
            const fittedMovingData = fitMovingData({...childMovingPoint, ...childMovingZoom}, childMinMax, 0);

            moveRef.current.style.transform = getStyleString(fittedMovingData);
            childStartPoint.x = fittedMovingData.x;
            childStartPoint.y = fittedMovingData.y;
            childStartZoom.zoom = fittedMovingData.zoom;
            childMovingPoint.x = fittedMovingData.x;
            childMovingPoint.y = fittedMovingData.y;
            childMovingZoom.zoom = fittedMovingData.zoom;

            setCurrentState({
                x: fittedMovingData.x,
                y: fittedMovingData.y,
                zoom: fittedMovingData.zoom,
            });
        }, [childMovingPoint, childMovingZoom, moveRef, childStartPoint, childStartZoom, movingLimit]);
    */

    /*
      UseEffect(() => {
            fitToFrame();
        }, [fitToFrame]);
    */

    useEffect(() => {
        const moveNode: HTMLDivElement = moveRef.current;

        if (isMovingActive) {
            moveNode.classList.add(useMoveElementStyle.use_move_element__moving);
        } else {
            moveNode.classList.remove(useMoveElementStyle.use_move_element__moving);
        }
    }, [moveRef, isMovingActive]);

    // Touch events
    useEffect(() => {
        const startNode: HTMLDivElement = startRef.current;
        const moveNode: HTMLDivElement = moveRef.current;
        let childMinMax = getChildMinMax(startRef.current, movingLimit);
        let activeMovingTimeoutId: number = Number.NaN;

        function onTouchStart(evt: TouchEvent): undefined {
            clearTimeout(activeMovingTimeoutId);
            activeMovingTimeoutId = window.setTimeout(() => {
                const firstStartTouch = evt.touches.item(0);
                if (!firstStartTouch) {
                    console.log("not first start touch");
                    return;
                }

                setIsMovingActive(true);

                onMoveStart({
                    moveNode,
                    point: touchToPoint(firstStartTouch),
                    startNode,
                });
                moveNode.style.transform = getStyleStringCentered({
                    ...touchToPoint(firstStartTouch),
                    ...childMovingZoom,
                });

                lastActiveMovingPointRef.current = touchToPoint(firstStartTouch);
            }, timeToBeginMovingMS);

            const firstTouch = evt.touches.item(0);
            const secondTouch = evt.touches.item(1);

            if (evt.touches.length === 1) {
                childMinMax = getChildMinMax(startRef.current, movingLimit);
            }

            if (firstTouch) {
                const firstPint = touchToPoint(firstTouch);

                moveTouchStartPoint.x = firstPint.x;
                moveTouchStartPoint.y = firstPint.y;
                lastActiveMovingPointRef.current = touchToPoint(firstTouch);
            }

            if (firstTouch && secondTouch) {
                zoomTouchStartPoint[0] = touchToPoint(firstTouch);
                zoomTouchStartPoint[1] = touchToPoint(secondTouch);
            }
        }

        // eslint-disable-next-line max-statements
        function onTouchMove(evt: TouchEvent): undefined {
            if (!isMovingActive) {
                clearTimeout(activeMovingTimeoutId);
                return;
            }

            const firstTouch = evt.touches.item(0);
            const secondTouch = evt.touches.item(1);

            if (firstTouch && secondTouch) {
                const lineBefore = getSize(zoomTouchStartPoint[0], zoomTouchStartPoint[1]);
                const lineAfter = getSize(touchToPoint(firstTouch), touchToPoint(secondTouch));
                const deltaZoom = lineAfter / lineBefore;

                childMovingZoom.zoom = fitValue({
                    max: childMinMax.maxZoom,
                    min: childMinMax.minZoom,
                    multiplier,
                    value: childStartZoom.zoom * deltaZoom,
                });

                // MoveNode.style.transform = getStyleString({...childMovingPoint, ...childMovingZoom});
                moveNode.style.transform = getStyleStringCentered({...touchToPoint(firstTouch), ...childMovingZoom});

                setCurrentState(
                    (currentStateToReplace: UseMoveElementConfigStateType): UseMoveElementConfigStateType => {
                        return {
                            ...currentStateToReplace,
                            zoom: childMovingZoom.zoom,
                        };
                    }
                );

                onMoving({
                    moveNode,
                    point: touchToPoint(firstTouch),
                    startNode,
                });
                lastActiveMovingPointRef.current = touchToPoint(firstTouch);
                return;
            }

            if (firstTouch && evt.touches.length === 1) {
                const firstPint = touchToPoint(firstTouch);

                const deltaX = moveTouchStartPoint.x - firstPint.x;
                const deltaY = moveTouchStartPoint.y - firstPint.y;

                childMovingPoint.x = fitValue({
                    max: childMinMax.maxX,
                    min: childMinMax.minX,
                    multiplier,
                    value: childStartPoint.x - deltaX,
                });

                childMovingPoint.y = fitValue({
                    max: childMinMax.maxY,
                    min: childMinMax.minY,
                    multiplier,
                    value: childStartPoint.y - deltaY,
                });

                // MoveNode.style.transform = getStyleString({...childMovingPoint, ...childMovingZoom});
                moveNode.style.transform = getStyleStringCentered({...touchToPoint(firstTouch), ...childMovingZoom});

                setCurrentState(
                    (currentStateToReplace: UseMoveElementConfigStateType): UseMoveElementConfigStateType => {
                        return {
                            ...currentStateToReplace,
                            ...childMovingPoint,
                        };
                    }
                );

                onMoving({
                    moveNode,
                    point: touchToPoint(firstTouch),
                    startNode,
                });
                lastActiveMovingPointRef.current = touchToPoint(firstTouch);
            }
        }

        function onTouchEnd(evt: TouchEvent): undefined {
            clearTimeout(activeMovingTimeoutId);

            const firstTouch = evt.touches.item(0);
            const secondTouch = evt.touches.item(1);

            if (firstTouch && secondTouch) {
                zoomTouchStartPoint[0] = touchToPoint(firstTouch);
                zoomTouchStartPoint[1] = touchToPoint(secondTouch);
            } else {
                childStartZoom.zoom = childMovingZoom.zoom;
                zoomTouchStartPoint[0] = {x: 0, y: 0};
                zoomTouchStartPoint[1] = {x: 0, y: 0};
            }

            if (firstTouch) {
                const firstPint = touchToPoint(firstTouch);

                moveTouchStartPoint.x = firstPint.x;
                moveTouchStartPoint.y = firstPint.y;
            } else {
                childStartPoint.x = childMovingPoint.x;
                childStartPoint.y = childMovingPoint.y;
            }

            if (!firstTouch) {
                if (isMovingActive) {
                    onMoveEnd({
                        moveNode,
                        point: lastActiveMovingPointRef.current,
                        startNode,
                    });
                }
                setIsMovingActive(false);
                // FitToFrame();
            }
        }

        if (isTouch) {
            startNode.addEventListener("touchstart", onTouchStart, false);
            startNode.addEventListener("touchmove", onTouchMove, false);
            startNode.addEventListener("touchend", onTouchEnd, false);
            startNode.addEventListener("touchcancel", onTouchEnd, false);
        }

        return (): void => {
            startNode.removeEventListener("touchstart", onTouchStart, false);
            startNode.removeEventListener("touchmove", onTouchMove, false);
            startNode.removeEventListener("touchend", onTouchEnd, false);
            startNode.removeEventListener("touchcancel", onTouchEnd, false);
        };
    }, [
        moveRef,
        onMoveEnd,
        isMovingActive,
        multiplier,
        movingLimit,
        childMovingPoint,
        moveTouchStartPoint,
        wrapperRef,
        startRef,
        childStartPoint,
        zoomTouchStartPoint,
        childMovingZoom,
        childStartZoom,
        timeToBeginMovingMS,
        onMoveStart,
        onMoving,
    ]);

    const animateMoveElementState = useCallback(
        async (updatedState: Partial<MovingDataType>): Promise<void> => {
            const filledNewState: MovingDataType = {
                ...childStartPoint,
                ...childStartZoom,
                ...updatedState,
            };

            const transformFromPoint = {
                transform: getStyleString({...childStartPoint, ...childStartZoom}),
            };

            const transformToPoint = {
                transform: getStyleString({
                    x: filledNewState.x * filledNewState.zoom,
                    y: filledNewState.y * filledNewState.zoom,
                    zoom: filledNewState.zoom,
                }),
            };

            const moveTransformList = [transformFromPoint, transformToPoint];

            const unitMoveTiming: KeyframeAnimationOptions = {
                duration: animationStepTime,
                easing: "ease-in-out",
                fill: "forwards",
                iterations: 1,
            };

            return new Promise((resolve: PromiseResolveType<void>) => {
                const animation = startRef.current.animate(moveTransformList, unitMoveTiming);

                animation.onfinish = (): undefined => {
                    childStartPoint.x = filledNewState.x * filledNewState.zoom;
                    childStartPoint.y = filledNewState.y * filledNewState.zoom;
                    childStartZoom.zoom = filledNewState.zoom;

                    childMovingZoom.zoom = childStartZoom.zoom;
                    childMovingPoint.x = childStartPoint.x;
                    childMovingPoint.y = childStartPoint.y;

                    startRef.current.style.transform = getStyleString({
                        ...childStartPoint,
                        ...childStartZoom,
                    });
                    animation.cancel();
                    resolve();
                };
            });
        },
        [animationStepTime, childMovingPoint, childMovingZoom, startRef, childStartPoint, childStartZoom]
    );

    // Mouse event
    useEffect(() => {
        const startNode: HTMLDivElement = startRef.current;
        const moveNode: HTMLDivElement = moveRef.current;
        let childMinMax = getChildMinMax(startRef.current, movingLimit);
        let activeMovingTimeoutId: number = Number.NaN;

        function onMouseDown(evt: MouseEvent): undefined {
            clearTimeout(activeMovingTimeoutId);
            activeMovingTimeoutId = window.setTimeout(() => {
                setIsMovingActive(true);

                onMoveStart({
                    moveNode,
                    point: mouseEventToPoint(evt),
                    startNode,
                });

                moveNode.style.transform = getStyleStringCentered({...mouseEventToPoint(evt), ...childMovingZoom});
            }, timeToBeginMovingMS);

            const mousePoint = mouseEventToPoint(evt);

            mousePressed.isPressed = true;

            childMinMax = getChildMinMax(startRef.current, movingLimit);

            moveTouchStartPoint.x = mousePoint.x;
            moveTouchStartPoint.y = mousePoint.y;
        }

        function onMouseMove(evt: MouseEvent): undefined {
            if (!mousePressed.isPressed) {
                return;
            }

            if (!isMovingActive) {
                clearTimeout(activeMovingTimeoutId);
                return;
            }

            const mousePoint = mouseEventToPoint(evt);
            const deltaX = moveTouchStartPoint.x - mousePoint.x;
            const deltaY = moveTouchStartPoint.y - mousePoint.y;

            childMovingPoint.x = fitValue({
                max: childMinMax.maxX,
                min: childMinMax.minX,
                multiplier,
                value: childStartPoint.x - deltaX,
            });

            childMovingPoint.y = fitValue({
                max: childMinMax.maxY,
                min: childMinMax.minY,
                multiplier,
                value: childStartPoint.y - deltaY,
            });

            // StartRef.current.style.transform = getStyleString({...childMovingPoint, ...childMovingZoom});
            moveNode.style.transform = getStyleStringCentered({...mousePoint, ...childMovingZoom});

            setCurrentState((currentStateToReplace: UseMoveElementConfigStateType): UseMoveElementConfigStateType => {
                return {
                    ...currentStateToReplace,
                    ...childMovingPoint,
                };
            });

            onMoving({
                moveNode,
                point: mouseEventToPoint(evt),
                startNode,
            });
        }

        function onMouseUp(evt: MouseEvent): undefined {
            clearTimeout(activeMovingTimeoutId);

            mousePressed.isPressed = false;

            zoomTouchStartPoint[0] = {x: 0, y: 0};
            zoomTouchStartPoint[1] = {x: 0, y: 0};
            childStartPoint.x = childMovingPoint.x;
            childStartPoint.y = childMovingPoint.y;

            setIsMovingActive(false);
            if (isMovingActive) {
                onMoveEnd({
                    moveNode,
                    point: mouseEventToPoint(evt),
                    startNode,
                });
            }

            // FitToFrame();
        }

        function onMouseUpStartNode(): undefined {
            clearTimeout(activeMovingTimeoutId);
        }

        if (!isTouch) {
            startNode.addEventListener("mousedown", onMouseDown, false);
            document.body.addEventListener("mousemove", onMouseMove, false);
            moveNode.addEventListener("mouseup", onMouseUp, false);
            startNode.addEventListener("mouseup", onMouseUpStartNode, false);
            // MoveNode.addEventListener("mouseleave", onMouseUp, false);
        }

        return (): void => {
            startNode.removeEventListener("mousedown", onMouseDown, false);
            document.body.removeEventListener("mousemove", onMouseMove, false);
            moveNode.removeEventListener("mouseup", onMouseUp, false);
            startNode.removeEventListener("mouseup", onMouseUpStartNode, false);
            // MoveNode.removeEventListener("mouseleave", onMouseUp, false);
        };
    }, [
        moveRef,
        onMoveEnd,
        multiplier,
        movingLimit,
        childMovingPoint,
        moveTouchStartPoint,
        wrapperRef,
        startRef,
        childStartPoint,
        zoomTouchStartPoint,
        childMovingZoom,
        childStartZoom,
        mousePressed,
        timeToBeginMovingMS,
        isMovingActive,
        onMoveStart,
        onMoving,
    ]);

    // Mouse wheel/zoom, not fixed for moving
    useEffect(() => {
        const wrapperNode: HTMLDivElement = wrapperRef.current;

        function onMouseWheel(evt: WheelEvent): undefined {
            const childMinMax = getChildMinMax(startRef.current, movingLimit);
            const {deltaY: mouseWheelDeltaY} = evt;

            if (!mouseWheelDeltaY) {
                return;
            }

            const zoomDirection: number = -mouseWheelDeltaY / Math.abs(mouseWheelDeltaY);

            // Const deltaZoom: number = (zoomDirection * (movingLimit.maxZoom - movingLimit.minZoom)) / 20;
            const deltaZoom: number = zoomDirection * 0.25;

            childMovingZoom.zoom = fitValue({
                max: childMinMax.maxZoom,
                min: childMinMax.minZoom,
                multiplier,
                value: childStartZoom.zoom + deltaZoom,
            });
            childStartZoom.zoom = childMovingZoom.zoom;

            startRef.current.style.transform = getStyleString({...childMovingPoint, ...childMovingZoom});
            // FitToFrame();
        }

        if (!isTouch) {
            wrapperNode.addEventListener("wheel", onMouseWheel, false);
        }

        return (): void => {
            wrapperNode.removeEventListener("wheel", onMouseWheel, false);
        };
    }, [
        // FitToFrame,
        multiplier,
        movingLimit,
        childMovingPoint,
        moveTouchStartPoint,
        wrapperRef,
        startRef,
        childStartPoint,
        zoomTouchStartPoint,
        childMovingZoom,
        childStartZoom,
        mousePressed,
    ]);

    const providedData: UseMoveElementHookType = useMemo((): UseMoveElementHookType => {
        return {animateMoveElementState, currentState};
    }, [animateMoveElementState, currentState]);

    return providedData;
}
