/* global localStorage */
const fontSizeSavedKey = "md-font-size";
// 16px is default font-size for markdown-pro
export const defaultMdFontSize = 16;

export const maxMdFontSize = 24;
export const minMdFontSize = 12;

export function getSavedMdFontSize(): number {
    if (typeof localStorage === "undefined") {
        return defaultMdFontSize;
    }

    const rawSavedFontSize: string = localStorage.getItem(fontSizeSavedKey) ?? defaultMdFontSize.toString(10);
    const savedFontSize: number = Number.parseInt(rawSavedFontSize, 10) || defaultMdFontSize;

    if (savedFontSize > maxMdFontSize) {
        return maxMdFontSize;
    }

    if (savedFontSize < minMdFontSize) {
        return minMdFontSize;
    }

    return savedFontSize;
}

export function saveMdFontSize(fontSizeForSave: number): undefined {
    localStorage.setItem(fontSizeSavedKey, fontSizeForSave.toString(10));
}
